.context-strip {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 1rem 1.5rem;
  margin-bottom: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  transition: all 0.2s ease;
}
.context-strip:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  border-color: #d1d5db;
}
.context-strip .context-content {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  flex-wrap: wrap;
}
.context-strip .context-content .context-title {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #374151;
  font-size: 0.875rem;
  font-weight: 600;
  white-space: nowrap;
}
.context-strip .context-content .context-title .context-icon {
  width: 1rem;
  height: 1rem;
  color: #3b82f6;
}
.context-strip .context-content .context-stats {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  flex-wrap: wrap;
}
.context-strip .context-content .context-stats .stat-item {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  padding: 0.375rem 0.75rem;
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 0.5rem;
  transition: all 0.2s ease;
}
.context-strip .context-content .context-stats .stat-item:hover {
  background: #f1f5f9;
  border-color: #cbd5e1;
}
.context-strip .context-content .context-stats .stat-item .stat-icon {
  width: 0.875rem;
  height: 0.875rem;
  color: #6b7280;
  flex-shrink: 0;
}
.context-strip .context-content .context-stats .stat-item .stat-icon.warning {
  color: #d97706;
}
.context-strip .context-content .context-stats .stat-item .stat-value {
  font-size: 0.875rem;
  font-weight: 700;
  color: #1f2937;
}
.context-strip .context-content .context-stats .stat-item .stat-value.warning {
  color: #d97706;
}
.context-strip .context-content .context-stats .stat-item .stat-label {
  font-size: 0.75rem;
  color: #6b7280;
  font-weight: 500;
}
.context-strip .context-loading .loading-skeleton {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  flex-wrap: wrap;
}
.context-strip .context-loading .loading-skeleton .skeleton-line {
  height: 1.5rem;
  background: linear-gradient(90deg, #f3f4f6 25%, #e5e7eb 50%, #f3f4f6 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 0.375rem;
}
.context-strip .context-loading .loading-skeleton .skeleton-line.w-24 {
  width: 6rem;
}
.context-strip .context-loading .loading-skeleton .skeleton-line.w-32 {
  width: 8rem;
}
.context-strip .context-loading .loading-skeleton .skeleton-line.w-28 {
  width: 7rem;
}
.context-strip .context-loading .loading-skeleton .skeleton-line.w-20 {
  width: 5rem;
}

@keyframes loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}
@media (max-width: 768px) {
  .context-strip {
    padding: 0.75rem 1rem;
  }
  .context-strip .context-content {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
  .context-strip .context-content .context-stats {
    gap: 0.75rem;
    width: 100%;
  }
  .context-strip .context-content .context-stats .stat-item {
    flex: 1;
    min-width: 0;
    justify-content: center;
  }
  .context-strip .context-loading .loading-skeleton {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
  }
  .context-strip .context-loading .loading-skeleton .skeleton-line {
    width: 100% !important;
  }
}
